$base-background: #242424;
$link-color: white;
$brick-color: #B0623F;
$patchwork-color: #878C54;
$quote-color: #cfc257;
$margin-row: 5px;

* {
        box-sizing: border-box;
}

html,body {
    margin: 0;
    background: $base-background;
    font-family: 'Roboto', sans-serif;
}

#centered_content {
    max-width: 1596px;
    background: $base-background;
    margin: auto;
    display: flex;
    flex-direction: column;
}

#splash {
    margin: 0;
    width: 2400px;
    margin-left: 50%;
    transform: translateX(-1200px);
}

p,h1,h2,h3 {
    margin-left: $margin-row;
}

p {
    margin-top: 12px;
    margin-bottom: 12px;
}

a {
    text-decoration: none;
    color: $link-color;
}

h2 {
    font-family: 'Days One', sans-serif;
    font-weight: normal;
    font-size: 15px;
    margin: 10px 0 0 0;
    padding: 0 3px 3px 3px;
    display: block;
    width: 100%;
    background: black;
    color: white;
}

#vision {
    height: 180px;
    margin: 40px 0 0 0;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#values {
    display: flex;
    justify-content: flex-start;
    align-content: center;
    flex-wrap: wrap;
}

#values header {
    order: 2;
    margin: 0;
    opacity: 0.7;
}

#values header:hover {
    opacity: 1;
    cursor: pointer;
}

#values header:hover ~ summary {
    opacity: 1;
}

#values header img {
    width: 228px;
}

#values summary {
    order: 1;
    width: 100%;
    height: 40px;
    margin: 0 auto 40px;
    color: #D0D0D0;
    font-family: 'IBM Plex Serif', serif;
    font-style: regular;
    font-size: 15px;
    opacity: 0;
    transition: opacity 1s;
}

#values summary p {
    margin: 0 auto;
    display: none;
    text-align: center;
}

blockquote {
    text-indent: 2em;
    margin: 30px 6em 30px 6em;
    font-family: 'IBM Plex Serif', serif;
    font-style: regular;
    font-size: 20px;
}

blockquote::before {
    content: "“";
}
   
blockquote::after {
    content: "„";
}

.tooltip {
    position: relative;
}

.tooltip-text {
    position: absolute;
    top: 0%;
    left: 0%;
    z-index: 1;
    opacity: 0;
    transition: opacity 1s;
    font-family: 'IBM Plex Serif', serif;
    font-style: italic;
    font-size: 15px;
    color: white;
    background-color: $brick-color;
    border-radius: 6px;
    padding: 6px 15px;
}

.tooltip:hover .tooltip-text {
    opacity: 0.8;
}

h1 {
    font-size: 20px;
    font-weight: bold;
}

h2 {
    font-size: 18px;
    font-weight: bold;
}

#values header h3 {
    margin: 0;
    padding: 0 $margin-row;
    font-size: 15px;
    font-weight: normal;
    color: white;
    background: black;
}

summary p {
    margin: 0.3em 0 0 0;
}

#topbar {
    color: white;
    background: black;
    display: flex;
}

#splash-container {
    background: black;
}

nav {
    flex: 1;
}

@media (max-width: 799px) {
    nav a {
	color: white;
    }
    
    a.current {
	color: black;
    }
    
    #splash-container {
	height: 15%;
    }

    #topbar #brand {
	font-size: 18px;
    }

    #topbar span {
	display: block;
    }

    #topbar span.separator {
	display: none;
    }
        
    .logo {
	font-family: 'Days One', sans-serif;
	font-weight: normal;
    }

    .subtitle {
	font-size: 15px;
	text-align: left;
	padding: 0 4px;
    }

    #topbar .logo {
	font-size: 22px;
	text-align: left;
	padding: 3px 6px 3px 3px;
    }

    #topbar nav {
	background: #BFBFBF;
	margin-top: 8px;
	font-weight: normal;
	font-size: 17px;
	text-align: left;
	padding: 3px 0;
    }
}

@media (min-width: 800px) {
    nav a {
	color: white;
    }

    a.current {
	color: gray;
    }

    #topbar #brand {
	font-size: 15px;
    }
    .logo {
	font-family: 'Days One', sans-serif;
	font-weight: normal;
    }

    #topbar .subtitle {
	font-size: 15px;
	text-align: left;
	padding: 0 4px;
    }

    #topbar .logo {
	font-size: 18px;
	text-align: left;
	padding: 3px 6px 3px 3px;
    }

    #topbar nav {
	background: black;
	font-weight: normal;
	font-size: 15px;
	text-align: right;
	padding: 3px 0;
    }
}

nav a {
    padding: 3px 3px 3px 5px;
    text-decoration: none;
}    

.subtitle {
    font-weight: bold;
    font-size: 16px;
}

#splash-container {
    width: 100%;
    overflow: hidden;
    margin: 0;
}

article img {
    float: left;
}

#observe-header:hover ~ summary #observe-preview {
    display: block;
}

#need-header:hover ~ summary #need-preview {
    display: block;
}

#constraints-header:hover ~ summary #constraints-preview {
    display: block;
}

#design-header:hover ~ summary #design-preview {
    display: block;
}

#metal-header:hover ~ summary #metal-preview {
    display: block;
}

#people-header:hover ~ summary #people-preview {
    display: block;
}

#simple-header:hover ~ summary #simple-preview {
    display: block;
}
